import { _decorator, Component, Node } from "cc";
import { basePageModel } from "../common/basePageModel";
import { loginView } from "./loginView";
import { GlobalConfig } from "../home/GlobalConfig";
const { ccclass, property } = _decorator;

export interface AxiosResponse<T = any, D = any> {
  data: T;
  status: number;
  statusText: string;
  headers: any;
  config: any;
  request?: any;
}

declare global {
  interface Window {
    axios: {
      post<T = any, R = AxiosResponse<T>, D = any>(
        url: string,
        data?: D,
        config?: any
      ): Promise<R>;
    };
    startCanvas: any;
    Telegram: any
  }
}

export enum walletType {}

export enum walletChain {}

export const loginUrls = {
  firstLoginCheck: "https://api.infinitytest.cc/api/v1/user/auth/first_login",
  tgLogin: "https://api.infinitytest.cc/api/v1/user/auth/tg_login",
  pwdLogin: "https://api.infinitytest.cc/api/v1/user/auth/pwd_login",
};

export interface firstLoginCheckDataType {
  id: number;
}

export interface firstLoginCheckResponseType {
  code: string;
  data: {
    firstLogin: true;
  };
  message: string;
}

export interface tgLoginDataType {
  authDate?: number;
  firstName?: string;
  hash?: string;
  id: number;
  inviteCode?: string;
  lastName?: string;
  loginChannel?: "GAME_LOBBY";
  photoUrl?: string;
  username?: string;
}

export interface tgLoginResponseType {
  code: string;
  data: {
    avatar: string;
    email: string;
    expiration: number;
    gamePoint: number;
    inviteCode: string;
    isNewUser: true;
    isSetPwd: true;
    token: string;
    totalPoint: 0;
    userId: number;
    userName: string;
    walletAddress: string;
    walletChain: "ETH";
    walletType: "METAMASK";
  };
  message: string;
}

export interface pwdLoginDataType {
  accountUserName: string;
  authDate?: number;
  firstName?: string;
  hash?: string;
  id: number;
  inviteCode?: string;
  lastName?: string;
  loginChannel?: "GAME_LOBBY";
  password: string;
  photoUrl?: string;
  username?: string;
}

export interface pwdLoginResponseType {
  code: string;
  data: {
    avatar: string;
    email: string;
    expiration: number;
    gamePoint: number;
    inviteCode: string;
    isNewUser: true;
    isSetPwd: true;
    token: string;
    userId: number;
    userName: string;
    walletAddress: string;
    walletChain: "ETH";
    walletType: "METAMASK";
  };
  message: string;
}

@ccclass("loginModel")
export class loginModel extends basePageModel {
  async start() {
    const isFirst = await this.checkLoginFirst();
    if (isFirst) {
      //展示登录页面
    } else {
      await this.tgLogin();
    }
  }

  update(deltaTime: number) {}

  async checkLoginFirst() {
    const isFirstLogin = await window.axios.post<firstLoginCheckResponseType>(
      loginUrls.firstLoginCheck,
      {
        id: window.Telegram.WebApp.initDataUnsafe.id,
      }
    );
    return isFirstLogin.data.data.firstLogin;
  }

  async tgLogin() {
    const loginData = await window.axios.post<tgLoginResponseType>(
      loginUrls.tgLogin,
      {
        authDate: 0,
        firstName: `${window.Telegram.WebApp.initDataUnsafe.firstName}`,
        hash: `${window.Telegram.WebApp.initDataUnsafe.hash}`,
        id: window.Telegram.WebApp.initDataUnsafe.id,
        lastName: `${window.Telegram.WebApp.initDataUnsafe.lastName}`,
        loginChannel: "GAME_LOBBY",
        photoUrl: '',
        username: ''
      }
    );

    if (loginData?.data?.data?.token) {
      //登录成功
      GlobalConfig._instance.token = loginData?.data?.data?.token
    }
  }

  async veteranLogin(){
    if(this.view.getComponent(loginView).accountInput.string.length || this.view.getComponent(loginView).pwdInput.string.length){
        
    }

    const loginData = await window.axios.post<pwdLoginResponseType>(
        loginUrls.pwdLogin,
        {
            accountUserName: this.view.getComponent(loginView).accountInput.string,
            authDate: 0,
            firstName: `${window.Telegram.WebApp.initDataUnsafe.firstName}`,
            hash: `${window.Telegram.WebApp.initDataUnsafe.hash}`,
            id: window.Telegram.WebApp.initDataUnsafe.id,
            lastName: window.Telegram.WebApp.initDataUnsafe.lastName,
            loginChannel: "GAME_LOBBY",
            password: this.view.getComponent(loginView).pwdInput.string,
            photoUrl: '',
            username: ''
        }
      );

      if(loginData?.data?.data?.token){
        GlobalConfig._instance.token = loginData?.data?.data?.token
        //this.view.getComponent(loginView)
        //跳转到
      }
  }
}
